@charset "UTF-8";
@font-face {
  font-family: Geologica;
  font-weight: 500;
  src: url(/font/Geologica-Medium.ttf);
}
@font-face {
  font-family: Geologica;
  font-weight: 400;
  src: url(/font/Geologica-Regular.ttf);
}
@font-face {
  font-family: Geologica;
  font-weight: 600;
  src: url(/font/Geologica-SemiBold.ttf);
}
@font-face {
  font-family: Manrope;
  font-weight: 700;
  src: url(/font/Manrope-Bold.ttf);
}
@font-face {
  font-family: Manrope;
  font-weight: 300;
  src: url(/font/Manrope-Light.ttf);
}
@font-face {
  font-family: Manrope;
  font-weight: 500;
  src: url(/font/Manrope-Medium.ttf);
}
@font-face {
  font-family: Manrope;
  font-weight: 400;
  src: url(/font/Manrope-Regular.ttf);
}
@font-face {
  font-family: Manrope;
  font-weight: 600;
  src: url(/font/Manrope-SemiBold.ttf);
}
.container {
  width: 100%;
  max-width: 1760px;
  margin: 0 auto;
  padding: 0 15px;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #f9f9fd;
}

::-webkit-scrollbar-thumb {
  background-color: var(--contrast-color);
  border-radius: 10px;
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
}

body {
  --main-background: #000000;
  --second-dark-color: #101010;
  --third-dark-color: #1d1d1d;
  --fourth-dark-color: #404040;
  --fifth-dark-color: #AFAFAF;
  --sixth-dark-color: #2B2B2B;
  --main-font: #FFFFFF;
  --second-light-color: #E2E2E2;
  --contrast-color: #B8FF20;
  --contrast-color-hover: #D9FF88;
  background-color: var(--main-background);
  color: var(--main-font);
  overflow-x: hidden;
  max-width: 100%;
  -webkit-text-size-adjust: none;
  font-size: 10px;
}

.background-text {
  font-family: "Geologica", sans-serif;
  font-size: clamp(36px, 5vw, 96px);
  font-weight: 600;
  line-height: 1.1;
  text-transform: uppercase;
  -webkit-text-stroke: 1px #757575;
  -webkit-text-fill-color: transparent;
}

.link {
  color: var(--main-font);
  font-weight: 400;
  line-height: 1.2;
  font-family: "Geologica", sans-serif;
  text-transform: uppercase;
  cursor: pointer;
}

.link:hover {
  color: var(--contrast-color);
}

.title-h1 {
  color: var(--main-font);
  font-family: "Geologica", sans-serif;
  font-weight: 600;
  font-size: clamp(34px, 8vw, 96px);
  line-height: 110%;
  text-transform: uppercase;
}

.title-h2 {
  color: var(--main-font);
  font-family: "Geologica", sans-serif;
  font-size: clamp(30px, 4vw, 72px);
  font-weight: 400;
  line-height: 1.1;
  text-transform: uppercase;
}

.title-h3 {
  color: var(--main-font);
  font-family: "Geologica", sans-serif;
  font-size: clamp(20px, 4vw, 54px);
  font-weight: 400;
  line-height: 1.1;
  text-transform: uppercase;
}

.xl-text {
  font-family: "Geologica", sans-serif;
  font-size: clamp(20px, 6.4vw, 40px);
  font-weight: 600;
  text-transform: uppercase;
  line-height: 1.25;
}

.big-text {
  color: var(--main-font);
  font-family: "Manrope", sans-serif;
  font-size: clamp(16px, 1.6vw, 36px);
  font-weight: 600;
  line-height: 1.1;
}

.text {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: clamp(16px, 2vw, 24px);
  line-height: 120%;
  color: var(--main-font);
}

.green-btn {
  background-color: var(--contrast-color);
  box-shadow: 0px 2px 16px 0px rgba(188, 255, 46, 0.28);
}

.green-btn:hover {
  background-color: var(--contrast-color-hover);
  box-shadow: 0px 2px 16px 0px rgba(188, 255, 46, 0.28);
}

.green-text {
  color: var(--contrast-color);
  font-family: "Geologica", sans-serif;
  font-size: 24px;
  font-weight: 500;
  line-height: 110%;
}

.s-text {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: clamp(14px, 1.5vw, 20px);
  line-height: 1.5;
}

.xs-text {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: clamp(14px, 1.5vw, 16px);
  line-height: 1.5;
}

body.lock {
  overflow: hidden;
}

.main.active {
  opacity: 0;
  transition: all 0.3s ease 0s;
}

.footer.active {
  opacity: 0;
  background-color: #000000;
  z-index: 20;
  transition: all 0.3s ease 0s;
}

.header .header__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: clamp(20px, 4vw, 40px);
  padding-bottom: clamp(20px, 4vw, 40px);
}
.header .logo {
  width: clamp(120px, 25vw, 201px);
}
.header .burger {
  display: flex;
  align-items: center;
  gap: 12px;
  z-index: 3;
  cursor: pointer;
}
.header .header__burger-text {
  font-family: "Geologica", sans-serif;
  font-size: 20px;
  line-height: 1.2;
  font-weight: 600;
  -moz-user-select: -moz-none;
  -o-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  text-transform: uppercase;
}
.header .header__burger {
  align-items: center;
  width: 29px;
  height: 17px;
  z-index: 3;
  position: relative;
}
.header .header__burger span {
  position: absolute;
  background-color: #fff;
  left: 6px;
  width: 23px;
  height: 2px;
  top: 7px;
  transition: all 0.3s ease 0s;
}
.header .header__burger::before,
.header .header__burger::after {
  content: " ";
  position: absolute;
  width: 23px;
  background-color: #fff;
  height: 2px;
  left: 0px;
  z-index: 5;
  transition: all 0.3s ease 0s;
  z-index: 5;
}
.header .header__burger::before {
  top: 0px;
}
.header .header__burger::after {
  bottom: 1px;
}
.header .header__burger.active:before {
  transform: rotate(45deg);
  top: 7px;
}
.header .header__burger.active::after {
  transform: rotate(-45deg);
  bottom: 8px;
}
.header .header__burger.active span {
  transform: scale(0);
  color: #828282;
}

.menus.active {
  height: 100vh;
  width: 100vw;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 0.2) 100%), radial-gradient(165.75% 70.71% at 50% 50%, #31046A 0%, #141414 100%);
}

.menu {
  position: fixed;
  overflow: auto;
  top: -120%;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease 0s;
  z-index: 7;
}

.menu.active {
  display: flex;
  top: 120px;
}

.menu.active::-webkit-scrollbar {
  width: 0;
}

.menu.active {
  -ms-overflow-style: none; /* IE и Edge */
  scrollbar-width: none; /* Firefox */
}

.menu__wrapper {
  min-height: 100%;
}

.menu__top {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: space-between;
  padding-top: clamp(35px, 4vw, 60px);
  padding-bottom: clamp(35px, 4vw, 60px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.menu__list {
  width: 120%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  position: relative;
}

.menu__list li:hover .menu__link {
  color: var(--contrast-color);
}

.menu__list::after {
  content: " ";
  background: rgba(255, 255, 255, 0.2);
  width: 1px;
  height: clamp(49vh, 45vw, 63vh);
  top: 0;
  right: 18%;
  position: absolute;
}

.menu__link {
  font-size: clamp(26px, 3vw, 40px);
  font-weight: 600;
  letter-spacing: 1.8px;
}

.menu__link.active {
  color: var(--contrast-color);
}

.sub-menu__list {
  width: 100%;
  position: fixed;
  right: -400%;
  transition: all 0.3s ease 0s;
}
.sub-menu__list li {
  padding-block: clamp(10px, 2vw, 15px);
}

.sub-menu__list li:hover .sub-menu__link {
  color: var(--contrast-color);
}

@media (hover: hover) and (min-width: 925px) {
  .menu__list li:nth-child(1):hover .sub-menu__list {
    width: 70%;
    position: absolute;
    right: -115%;
    right: -70%;
    top: 0;
    display: flex;
    flex-direction: column;
  }
  .menu__list li:nth-child(1):hover .sub-menu__list li {
    padding-block: clamp(10px, 2vw, 15px);
  }
}
.sub-menu__list::after {
  content: " ";
  background: rgba(255, 255, 255, 0.2);
  width: 1px;
  height: clamp(49vh, 45vw, 63vh);
  top: 0;
  right: 6%;
  position: absolute;
}

.sub-menu__link {
  font-size: clamp(22px, 2.5vw, 40px);
  letter-spacing: 0.8px;
}

.sub-menu__link.active {
  color: var(--contrast-color);
}

.sub-sub-menu__list {
  position: fixed;
  right: -400%;
  transition: all 0.5s ease 0s;
  height: 100%;
  padding-inline: 50px;
  transform: translateX(-50px);
}
.sub-sub-menu__list li {
  padding-block: clamp(8px, 2vw, 15px);
}

@media (hover: hover) and (min-width: 925px) {
  .sub-menu__list > li:hover .sub-sub-menu__list {
    position: absolute;
    width: 162%;
    right: -170%;
    top: 0;
    display: flex;
    flex-direction: column;
    padding-inline: 50px;
    transform: translateX(-50px);
  }
  .sub-menu__list > li:hover .sub-sub-menu__list li {
    padding-block: clamp(8px, 2vw, 15px);
  }
}
.sub-sub-menu__link {
  font-size: clamp(16px, 1.5vw, 24px);
  letter-spacing: 0.48px;
}

.sub-menu__list.open {
  width: 70%;
  position: absolute;
  right: -115%;
  right: -70%;
  top: 0;
  display: flex;
  flex-direction: column;
}
.sub-menu__list.open li {
  padding-block: clamp(10px, 2vw, 15px);
}

.sub-sub-menu__list.open {
  position: absolute;
  width: 162%;
  right: -170%;
  top: 0;
  display: flex;
  flex-direction: column;
  padding-inline: 50px;
  transform: translateX(-50px);
}
.sub-sub-menu__list.open li {
  padding-block: clamp(8px, 2vw, 15px);
}

.menu__bottom {
  margin: 50px 0 0 0;
  height: 350px;
}

.bottom-menu__list {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.bottom-menu__link {
  font-size: clamp(22px, 2vw, 36px);
}

.bottom-menu__btn {
  font-family: "Manrope", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  color: #404040;
  padding: 24px 35px;
  text-align: center;
  border-radius: 8px;
}

@media (max-width: 925px) {
  .menu__list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  .menu__list::after {
    display: none;
  }
  .sub-menu__list.open {
    position: relative;
    left: 15px;
    top: 10px;
  }
  .sub-menu__list.open::after {
    display: none;
  }
  .sub-sub-menu__list.open {
    position: relative;
    left: 15px;
    top: 10px;
  }
  .bottom-menu__list {
    flex-direction: column;
    align-items: start;
  }
  .bottom-menu__list li:last-child {
    margin-top: 15px;
  }
  .bottom-menu__btn {
    padding: 10px 20px;
  }
}
@media (max-width: 500px) {
  .menu.active {
    display: flex;
    top: 70px;
  }
  .header .header__burger-text {
    font-size: 13px;
  }
  .header .burger {
    gap: 5px;
  }
  .header .header__burger {
    width: 16px;
    height: 9px;
  }
  .header .header__burger span {
    left: 3px;
    width: 13px;
    height: 1px;
    top: 4px;
  }
  .header .header__burger::before,
  .header .header__burger::after {
    width: 13px;
    height: 1px;
    left: 0px;
  }
  .header .header__burger::before {
    top: 0px;
  }
  .header .header__burger::after {
    bottom: 0px;
  }
  .header .header__burger.active:before {
    top: 4px;
  }
  .header .header__burger.active::after {
    bottom: 4px;
  }
}
.footer {
  margin-top: 100px;
}
.footer .footer__akticom {
  color: var(--main-font);
  font-family: "Geologica", serif;
  font-size: clamp(12px, 1vw, 14px);
  font-weight: 400;
  line-height: 1.7px;
  letter-spacing: 0.1px;
}
.footer .footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 15px;
  border-top: 1px solid var(--main-font);
  padding-top: clamp(30px, 3vw, 50px);
  padding-bottom: 20px;
}
.footer .footer__left {
  display: flex;
  flex-direction: column;
  gap: clamp(30px, 3vw, 50px);
}
.footer .footer__list {
  display: flex;
  gap: clamp(30px, 3vw, 60px);
}
.footer .footer__link {
  color: var(--main-font);
  font-family: "Geologica", serif;
  font-size: clamp(16px, 1.5vw, 20px);
  font-weight: 400;
  line-height: 1.2;
}
.footer .footer__phone {
  display: flex;
  gap: clamp(10px, 2vw, 30px);
}
.footer .footer__link-phone {
  color: var(--main-font);
  font-family: "Geologica", serif;
  font-size: clamp(14px, 1.3vw, 18px);
  font-weight: 600;
  line-height: 1.5;
}

@media (max-width: 900px) {
  .footer .footer__wrapper {
    flex-wrap: wrap;
    gap: 25px;
  }
  .footer .footer__list {
    flex-wrap: wrap;
  }
  .footer .footer__phone {
    flex-direction: column;
  }
  .footer .footer__logo img {
    max-width: 80%;
  }
}
.case-done .analitics {
  margin: 0;
}

.case-name__wrapper {
  display: flex;
  justify-content: space-between;
  padding-block: clamp(20px, 2vw, 40px);
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}

.case-name__wrapper--services {
  display: flex;
  justify-content: space-between;
  padding-block: clamp(30px, 2vw, 40px);
  border-top: 1px solid #d9d9d9;
  border-bottom: none;
}

.case-name__btns {
  display: flex;
  gap: 10px;
}

.case-task__wrapper {
  padding-block: 40px;
  border-bottom: 1px solid #d9d9d9;
}

.case-task__photo {
  display: flex;
  justify-content: center;
}
.case-task__photo img {
  width: 100%;
}

.case-task__info {
  padding: 80px 0 40px 0;
}

.display-case {
  display: grid;
  grid-template-columns: 35% 1fr;
  padding: 80px 0 40px 0;
}

.display-case--single-service {
  padding: 50px 0 20px 0;
}

.case-done__wrapper {
  padding-bottom: 60px;
  border-bottom: 1px solid #d9d9d9;
}

.case-done__title {
  padding-top: 30px;
}

.case-done__punkts .analitics__list {
  width: 100%;
}

.case-concept__wrapper {
  padding-top: 40px;
  padding-bottom: 80px;
  border-bottom: 1px solid #d9d9d9;
}

.case-concept__imgs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: clamp(10px, 2vw, 30px);
}

.case-result__wrapper {
  padding-block: 80px;
  border-bottom: 1px solid #d9d9d9;
}

.case-result-block__title {
  color: var(--contrast-color);
}

.case-result__numbers {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
}

.case-result__block {
  display: grid;
  gap: clamp(10px, 1.7vw, 20px);
}

.case-result-block__text {
  font-family: "Manrope";
  font-weight: 500;
  font-size: clamp(12px, 2.2vw, 24px);
  line-height: 110%;
  color: var(--main-font);
}

.case-single__case-result {
  margin-bottom: -110px;
}

@media (max-width: 1024px) {
  .case-result__numbers {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
  .case-single__case-result {
    margin-bottom: -80px;
  }
}
@media (max-width: 768px) {
  .display-case {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 30px 0 40px 0;
  }
  .case-concept__imgs {
    display: flex;
    overflow-x: scroll;
    -ms-overflow-style: none; /* IE и Edge */
    scrollbar-width: none; /* Firefox */
  }
  .case-concept__imgs ::-webkit-scrollbar {
    width: 0;
  }
  .case-concept__imgs img {
    width: 310px;
    height: 200px;
    object-fit: cover;
  }
  .case-concept__wrapper {
    border-bottom: none;
  }
}
@media (max-width: 500px) {
  .case-single__case-result {
    margin-bottom: -40px;
  }
  .case-done__wrapper {
    padding-bottom: 30px;
    border-bottom: none;
  }
}
.nav-case {
  display: flex;
  gap: clamp(10px, 2vw, 25px);
  flex-wrap: wrap;
  padding-block: 40px;
  border-bottom: 1px solid #d9d9d9;
}

.nav-case__item {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--main-font);
  border-radius: 100px;
  height: fit-content;
  padding: 12px 20px;
  cursor: pointer;
  -moz-user-select: -moz-none;
  -o-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  color: var(--main-font);
  font-family: "Geologica", serif;
  font-size: clamp(14px, 2vw, 20px);
  font-weight: 400;
  line-height: 1.1;
}

.nav-case__item:active,
.nav-case__item.active {
  color: var(--contrast-color);
  border: 1px solid var(--contrast-color);
}

.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--main-background);
  z-index: 9999999;
  transition: all 1s ease 0s;
  opacity: 1;
  visibility: visible;
}
.preloader .loader {
  position: absolute;
  right: 10%;
  top: 5%;
  color: var(--main-font);
  font-family: "Geologica", serif;
  font-size: clamp(76px, 2vw, 128px);
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: 2.56px;
  text-transform: uppercase;
}
.preloader .progress-bar {
  position: relative;
  margin-top: 30px;
  width: 100%;
  height: 6px;
  background: var(--sixth-dark-color);
  border-radius: 15px;
}
.preloader .progress {
  position: absolute;
  top: 0;
  left: 0;
  background: var(--main-font);
  height: 6px;
  border-radius: 15px;
}

.preloader.done {
  opacity: 0;
  visibility: hidden;
}

.preloader-logo {
  display: flex;
  justify-content: end;
  margin-top: 50px;
}

.preloader-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 30px;
  margin-top: 70px;
}

.preloader-num {
  border-radius: 50%;
  border: 1px solid var(--main-font);
  font-family: "Geologica", sans-serif;
  font-size: clamp(18px, 1.6vw, 36px);
  font-weight: 600;
  line-height: 1.1;
  padding: clamp(10px, 2vw, 20px);
}

.preloader-name {
  font-family: "Geologica", sans-serif;
  font-size: clamp(28px, 6vw, 102px);
  font-weight: 600;
  line-height: 1.1;
}

._anim-fade-in-down {
  transform: translate(0px, -120px);
  transition: all 1.2s ease 0s;
  opacity: 0;
}

._anim-fade-in-down._active {
  transform: translate(0px, 0px);
  transition: all 1.2s ease 0s;
  opacity: 1;
}

._anim-fade-in-top {
  transform: translate(0px, 120px);
  transition: all 1.2s ease 0s;
  opacity: 0;
}

._anim-fade-in-top._active {
  transform: translate(0px, 0px);
  transition: all 1.2s ease 0s;
  opacity: 1;
}

._anim-fade-in-left {
  transform: translate(-320px, 0px);
  transition: all 1s ease 0s;
  opacity: 0;
}

._anim-fade-in-left._active {
  transform: translate(0px, 0px);
  transition: all 1s ease 0s;
  opacity: 1;
}

._anim-fade-in-right {
  transform: translate(320px, 0px);
  transition: all 1s ease 0s;
  opacity: 0;
}

._anim-fade-in-right._active {
  transform: translate(0px, 0px);
  transition: all 1s ease 0s;
  opacity: 1;
}

._anim-fade-in-center {
  transition: all 1s ease 0s;
  opacity: 0;
}

._anim-fade-in-center._active {
  transition: all 1s ease 0s;
  opacity: 1;
}

._anim-fade-in-left-delay {
  animation: in-left-delay 1s ease;
}

@keyframes in-left-delay {
  0% {
    transform: translate(-320px, 0px);
    opacity: 1;
  }
  100% {
    transform: translate(0px, 0px);
    opacity: 1;
  }
}
._anim-from-left {
  transform: translate(-120%, 0px);
  transition: all 0.9s ease 0s;
}

._anim-from-left._active {
  transform: translate(0px, 0px);
  transition: all 0.9s ease 0s;
}

._anim-from-right {
  transform: translate(120%, 0px);
  transition: all 0.9s ease 0s;
}

._anim-from-right._active {
  transform: translate(0px, 0px);
  transition: all 0.9s ease 0s;
}

._anim-from-right-bot {
  transform: translate(120%, 50%);
  transition: all 0.9s ease 0s;
}

._anim-from-right-bot._active {
  transform: translate(0px, 0px);
  transition: all 0.9s ease 0s;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slidein {
  0% {
    left: -100%;
  }
  100% {
    left: 0;
  }
}
