}
.spoiler-btn_more {
  font-family: "Manrope", sans-serif;
  font-size: clamp(14px, 1.4vw, 20px);
  font-weight: 400;
  color: var(--main-background);
  padding: 5px 10px;
  text-align: center;
  border-radius: 5px;
}
