}

.case-spoiler__right {
}

.spoiler-item {
  display: grid;
  border-bottom: 1px solid #a1a1a1;
  position: relative;
  cursor: pointer;
  padding: 40px;
  align-items: start;
}

.spoiler-item.active {
  background-color: var(--contrast-color);
}

.spoiler-item.active .green-text,
.spoiler-item.active .spoiler__name {
  color: var(--second-dark-color);
}
.spoiler-item.active .spoiler-btn_more {
  color: var(--contrast-color);
  background-color: var(--main-background);
}

.spoiler__text {
  right: 100%;
  display: block;
  position: absolute;
  z-index: 7;
  max-width: 80%;
  transition: all 0.5s ease 0s;
  top: 120px;
}

.spoiler__text img {
  position: relative;
  z-index: 10;
}

.spoiler-item.active .spoiler__text {
  transition: all 0.5s ease 0s;
  right: 0;
}

.spoiler__arrow {
  position: absolute;
  right: 0;
  top: 48px;
  margin-right: 10px;
}
.spoiler__arrow svg {
  transition: all 0.3s ease 0s;
}

.spoiler-item.active .spoiler__arrow svg {
  transform: rotate(-180deg);
  transition: all 0.3s ease 0s;
}

.arrow {
  display: block;
  width: 26px;
  height: 26px;
  position: relative;
  z-index: 3;
}
.arrow span {
  position: absolute;
  background-color: var(--contrast-color);
  left: 0;
  width: 120%;
  height: 3px;
  top: 10px;
  transition: all 0.3s ease 0s;
  transform: rotate(-45deg);
}

.arrow::before,
.arrow::after {
  content: " ";
  position: absolute;
  width: 100%;
  background-color: var(--contrast-color);
  height: 3px;
  transition: all 0.5s ease 0s;
}
.arrow::before {
  top: 0;
}
.arrow::after {
  top: 12px;
  margin-left: 13px;
  transform: rotate(-90deg);
}
.spoiler-item.active .arrow span {
  background-color: var(--main-background);
}
.spoiler-item.active .arrow::before {
  right: 10px;
  top: 10px;
  transform: rotate(-90deg);
  background-color: var(--main-background);
}
.spoiler-item.active .arrow::after {
  top: 22px;
  right: -2px;
  transform: rotate(0deg);
  background-color: var(--main-background);
}

@media (max-width: 768px) {
  .case-spoiler__right {
    display: none;
  }
  .case-spoiler__wrapper {
    display: grid;
    grid-template-columns: 1fr;
  }







